<template>
  <vue-form
    :schema="schema"
    :definition="definition"
    :model="model"
  >
    <div class="form-group form-action">
      <div class="col-sm-offset-2 col-sm-10">
        <button type="button" class="btn btn-primary" @click="submit"></button>
      </div>
    </div>
  </vue-form>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  data () {
    return {
      schema: {
        title: 'basic',
        type: 'object',
        properties: {
          name: {
            type: 'string',
            title: ''
          },
          age: {
            type: 'number',
            title: '',
            minimum: 1,
            maximum: 120,
            default: 25
          },
          single: {
            type: 'boolean',
            title: ''
          },
          sex: {
            type: 'string',
            title: '',
            enum: ['0', '1'],
            default: '0'
          },
          hobby: {
            title: '',
            type: 'array',
            items: {
              type: 'string',
              title: '',
              enum: ['', '', '']
            }
          },
          other: {
            'title': '',
            'type': 'string',
            'maxlength': 100
          },
          birthday: {
            title: '',
            type: 'string',
            format: 'date-time'
          },
          group: {
            title: '',
            type: 'array',
            items: {
              type: 'string',
              title: ''
            }
          }
        },
        'required': ['name', 'age', 'single', 'sex', 'hobby', 'other', 'birthday']
      },
      definition: [
        {
          type: 'html',
          tpl: '<div class="alert alert-info" role="alert"></div>'
        },
        {
          key: 'name',
          lazy: false
        },
        'age',
        'single',
        {
          key: 'sex',
          options: [
            {
              text: '',
              value: '0'
            },
            {
              text: '',
              value: '1'
            }
          ]
        },
        'hobby',
        {
          key: 'other',
          type: 'textarea'
        },
        {
          key: 'birthday',
          type: 'date',
          beforeDay: '2019-01-25',
          options: {
            type: 'datetime',
            format: 'YYYY-MM-DD HH:mm:ss'
          }
        },
        // {
        //   key: 'birthday',
        //   min: '2016-1-15'
        // }
        // {
        //   key: 'group',
        //   type: 'suggestion',
        //   options: {
        //     '1688': 0,
        //     'tmall': 1,
        //     'taobao': 2,
        //     'alipay': 3
        //   },
        //   'multiple': true
        // }
      ],
      model: {
        name: '',
        hobby: ['']
      }
    }
  },
  computed: {
    ...mapState({
      data: state => state.model,
      valid: state => state.valid
    }),
  },
  methods: {
    submit () {
      console.log(this.data)
      this.validate()

      if (this.valid) {
        console.log('submit form !')
      }
    },
    ...mapMutations([
      'validate'
    ])
  }
}
</script>



